        display: inline-block;
        width: 155px;
        text-align: left;
        margin-right: 45px;
        overflow: hidden;
        vertical-align: middle;
      }
      .test-upload {
        width: 65px;
        height: 65px;
        border: 1px solid #333;
      }
    }
    .v-title {
      text-align: left;
      font-size: 14px;
      color: #333;
    }
  }
  #captcha {
    margin-bottom: 10px;
  }
  .com-input {
    &.v-input {
      width: 435px;
